import { Grid } from '@mui/material';
import Box from '@mui/material/Box';
import Typer from './Courses/Typer';


const Courses = () => {
    return (
<Box sx={{ justifyContent: 'center', alignItems: 'center'}}>
  <Grid sx={{ flexGrow: 1 }} container spacing={2}>
      <Grid item xs={12}>
        <Grid container sx={{justifyContent:"center"}} spacing={2}>
            <Grid key={0} item>      
            <Typer text='Advanced JavaScript' /> 
            <Typer text='Advanced Java' /> 
            <Typer text='Object Oriented Approach & UML' /> 
            <Typer text='Advanced Algorithms' /> 
            <Typer text='Software Industrialization and DevOps' /> 
            </Grid>
            <Grid key={1} item>
            <Typer text='Advanced C' /> 
            <Typer text='.Net And C# Programming' /> 
            <Typer text='Cloud Computing with AWS' /> 
            <Typer text='Block Chain Bitcoin and Security Fundamental' /> 
            <Typer text='Web Applications Patterns' /> 
            </Grid>
            <Grid key={2} item>
            <Typer text='Software & Database Security' /> 
            <Typer text='iOS Programming with Swift' /> 
            <Typer text='Digital Risk Management' /> 
            <Typer text='Data Privacy by Design' /> 
            <Typer text='Operating Systems - UNIX/LINUX' /> 
            </Grid>
        </Grid>
      </Grid>
    </Grid>
  </Box>
    )
}

export default Courses
