import { Paper } from '@mui/material';
import Typography from '@mui/material/Typography';
import Typewriter from 'typewriter-effect'

const Typer = ({text}) => {
    return (
        <Paper display="flex" sx={{ justifyContent:'center', marginBottom: 2, width: 300, display: 'center', background: '#2A2C2D', border: '1px dashed grey', padding: 0.5}}  variant="outlined">
        <Typography variant="subtitle2" color="#D7E7F5" style={{marginLeft:'auto', marginRight:'auto' ,flexGrow: 1,
textAlign: "center"}}> 
        <Typewriter 
        options={{
        autoStart: true,
        loop: true,
        }}
        onInit= {(typewriter) => {
            typewriter
            .changeDelay((50))
            .typeString(text)
            .pauseFor((3000))
            .deleteAll()
            .pauseFor((500))
            .start();
        }}
        />
            </Typography>
      </Paper>
    )
}

export default Typer
