import React from 'react'
import ProjectCard from './ProjectCard'
import { Grid } from '@mui/material'
const Projects = () => {
    return (
        <Grid container spacing={2} sx={{justifyContent:'center'}}>
        <Grid item xs={12} md={4} sm={6}>
        <ProjectCard demo="https://mymemories-project.netlify.app/" name="MERN Stack - Memories" image="./images/memories.jpg" 
        description="Full Stack Web Application to post your souvenirs (Photos) With details with the ability of other users to like them. Mini Social Network" 
        gitLink="https://github.com/hafeed06/Memories-Mern"
        technos="ReactJS, NodeJS, Express, Redux, MongoDB, MUI, Google Auth"/>
        </Grid>
        <Grid item xs={12} md={4} sm={6}>
        <ProjectCard demo="https://hafid-tasktracker.netlify.app/" name="Task Tracker - ReactJS" image="./images/tasktracker.jpg" 
        description="Task Tracker - A simple Front-End web application designed to serve as an Agenda to Track upcoming Event with the option to mark some as Important" 
        technos="ReactJS, Material UI, JavaScript, HTML5, CSS3"
        gitLink="https://github.com/hafeed06/React-Task-Tracker"/>
        </Grid>
        <Grid item xs={12} md={4} sm={6}>
        <ProjectCard demo="https://hafid-expensetracker.netlify.app/" name="Expense Tracker - ReactJS" image="./images/expensetracker.jpg" 
        description="Expense Tracker - Like Task Tracker, but this one is for tracking Expenses! Put all the expenses and ressources you've had
        and it will generate your balance for you with details in a nice outlook" 
        technos="ReactJS, Material UI, JavaScript, HTML5, CSS3"
        gitLink="https://github.com/hafeed06/expensetracker"/>
        </Grid>
        <Grid item xs={12} md={4} sm={6}>
        <ProjectCard demo="https://hafid.co/projects/greenit/" name="Design4Green Internation Challenge" image="./images/design4green.jpg" 
        description="Design4Green International Challenge. Eco Friendly Web App that reads from CSV File best GreenIT Practices, asks user to add some to their basket,
        and generate results in a PDF"
        technos="HTML5, CSS3, Javascript, jQuery, BootStrap, Service Workers, GZiping of contents, JSPDF Library"
        gitLink="https://github.com/hafeed06/Design4Green"/>
        </Grid>

        <Grid item xs={12} md={4} sm={6}>
        <ProjectCard demo="" name="Selnium Amazon Auto Buyer Bot" image="./images/AmazonBot.jpg" 
        description="This is a small project that was part of our course for using Selenium with Python to create a bot that automatically
        goes on Amazon and purchases what you tell it to purchase. It used the ChromeDriver and thus needs the user to have Chrome"
        technos="Python, Selenium Dash"
        gitLink=""/>
        </Grid>

      </Grid>
    )
}

export default Projects
