import { Typography } from '@mui/material';
import Typewriter from 'typewriter-effect'
import {useEffect} from 'react'


const Cover = () => {
  let navHeight = 0
  useEffect(() => {
    var navHeight = document.getElementById("myAppBar").offsetHeight
    document.getElementById("mainCover").style.marginTop = navHeight+"px"
    document.getElementById("mainCover").style.minHeight= window.innerHeight-navHeight+"px"
    window.addEventListener('resize', function() {
      // console.log("Change Detected")
      var navHeight = document.getElementById("myAppBar").offsetHeight
      document.getElementById("mainCover").style.marginTop = navHeight+"px"
      document.getElementById("mainCover").style.minHeight= window.innerHeight-navHeight+"px"
    });
  })
    return (
          <div id="mainCover" style={{backgroundImage:'url(./images/cover5.jpg)'}}>
            <div style={{marginLeft:'auto',marginRight:'auto'}}>
            <Typography variant="h2" pl={2} pt={2} color="white" align='left'>
              I am ... 
              </Typography>
              <Typography variant="h2" pl={2} pt={2} color="white" align='left'>
              <Typewriter 
              options={{
                autoStart: true,
                loop: true,
              }}
              onInit= {(typewriter) => {
                  typewriter
                  .changeDelay(50)
                  .typeString('<strong> a <span style="color:#09922E">Software</span> Engineer</strong>')
                  .deleteAll()
                  .typeString('<strong>a <span style="color:#920978">Full Stack</span> Developer</strong>')
                  .deleteAll()
                  .typeString('<strong>a <span style="color:#920978">PHP </span> Developer</strong>')
                  .deleteAll()
                  .typeString('<strong>an <span style="color:#920978">Optimization Oriented</span> Person</strong>')
                  .deleteAll()
                  .typeString('<strong> JavaScript Addict</strong>')
                  .deleteAll()
                  .typeString('<strong> Java Architect</strong>')
                  .pauseFor(1000)
                  .start();
                }}
              />
              </Typography>
            </div>
          </div>
    )
}

export default Cover
