import * as React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { Button, CardActionArea, CardActions } from '@mui/material';
import GitHubIcon from '@mui/icons-material/GitHub';
import PreviewIcon from '@mui/icons-material/Preview';
import { ThemeProvider } from '@mui/material/styles';
import theme from '../theme';
import CodeIcon from '@mui/icons-material/Code';

const ProjectCard = ({name, image, description, demo, gitLink, technos}) => {
    return (
        <Card style={{background:"#35393C", border:'1px dashed grey'}} variant="outlined">
        <CardActionArea>
          <CardMedia onClick={() => window.open(demo, '_blank').focus()}
            component="img"
            height="300"
            image={image}
            alt=""
            style={{opacity:1}}
          />
          <CardContent style={{color:"#A4D1F9"}}>
            <Typography gutterBottom variant="h5" component="div">
              {name}
            </Typography>
            <Typography variant="body2" color="#EBEBEC">
              {description}
            </Typography>
              <CodeIcon />
              <Typography variant="body2" sx={{'fontWeight':'bold'}}> 
              {technos}
              </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions style={{background:"#444A50"}}>
        <ThemeProvider theme={theme}>
          <Button size="small" color="secondary" variant="contained" startIcon={<PreviewIcon />}>
          <a href={demo} rel="noreferrer" target="_blank">View Live</a>
          </Button>
          <Button size="small" color="primary" variant="outlined" startIcon={<GitHubIcon />}>
          <a href={gitLink} rel="noreferrer" target="_blank">GitHub</a>
          </Button>
          </ThemeProvider>
        </CardActions>
      </Card>
    )
}

export default ProjectCard
