import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Button from '@mui/material/Button';
import GitHub from '@mui/icons-material/GitHub';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import MailOutlineIcon from '@mui/icons-material/MailOutline';
import Stack from '@mui/material/Stack';
import { Link } from '@mui/material';



const Navbar = () => {

    return (
        <Box sx={{ flexGrow: 1 }}>
        <AppBar position="fixed" className="myAppBar" id="myAppBar">
          <Toolbar>
          {/* <Typography variant="small" component="div"> 
          Developer Timeline | Projects | Languages and Technologies
          </Typography> */}
          <Stack spacing={1} direction="row">
            <Button href="#timeline" variant="contained">Developer Timeline</Button>
            <Button href="#myprojects"variant="contained">Projects</Button>
            <Button href="#technologies" variant="contained">Languages and Technologies</Button>
          </Stack>
            {/* <Typography variant="h6" component="div" sx={{ flexGrow: 1 }} gutterBottom> 
              Abdelhafid ElBekkaoui - Full Stack Developer
            </Typography> */}
            <div style={{marginLeft:'auto'}}>
            <Link href="https://github.com/hafeed06" target="_blank" style={{textDecoration:"none", color:"white"}}>
            <GitHub sx={{padding:1, marginLeft:0.1, transform: 'scale(1.5)'}}/>
            </Link>
            <Link href="https://www.linkedin.com/in/abdelhafid-elbekkaoui-365657213/" target="_blank" style={{textDecoration:"none", color:"white"}}>
            <LinkedInIcon sx={{padding:1, marginLeft:0.1, transform: 'scale(1.5)'}}/>
            </Link>
            <Link href="mailto:hafid@hafid.co" target="_blank" style={{textDecoration:"none", color:"white"}}>
            <MailOutlineIcon sx={{padding:1, marginLeft:0.1, transform: 'scale(1.5)'}}/>
            </Link>

            </div>
          </Toolbar>
        </AppBar>
      </Box>
    )
}

export default Navbar
